html {
  --background-color: #fff;
  --active-background-color: #eee;
  --accent-color: #8b0000;
  --text-color: #000;
  --title-color: #2f4f4f;
  --font-weight: 700;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.people {
  grid-template-columns: repeat(2, 1fr);
  gap: 40px 30px;
  max-width: 454px;
  margin: 0 auto;
  padding: 30px 35px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 1fr);
    max-width: 970px;
    padding-left: 55px;
    padding-right: 55px;
  }
}

.person {
  padding: 16px 10px;
  transition: transform .5s linear, background-color .5s linear, border-radius .5s linear;
  position: relative;
}

.person:hover {
  background-color: var(--active-background-color);
  border-radius: 10px;
  transform: scale(1.3);
}

.person__photo-wrap {
  position: relative;
}

.person__photo {
  border-radius: 200px;
  width: 200px;
  height: 200px;
  margin-bottom: 8px;
  transition: border-radius 1s ease-in-out;
  display: block;
}

.person:hover .person__photo {
  border-radius: 60px;
}

.person__rank {
  background-color: var(--background-color);
  color: var(--accent-color);
  font-weight: var(--font-weight);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  display: flex;
  position: absolute;
  top: 84px;
  right: 10px;
}

.person__details {
  position: relative;
}

.person__name {
  color: var(--title-color);
  font-size: 20px;
  font-weight: var(--font-weight);
  justify-content: center;
  align-items: center;
  margin-bottom: 9px;
  display: flex;
}

.person__extra {
  text-align: center;
  color: var(--text-color);
  justify-content: center;
  font-size: 18px;
  display: flex;
}
